export let countdown = (endTime)=>{
		// 
		const end = endTime;
		// 
		const now = Date.parse(new Date())
		
		// 
		const msec = end - now
		let hr = parseInt(msec / 1000 / 60 / 60 % 24)//
		let min = parseInt(msec / 1000 / 60 % 60)//
		let sec = parseInt(msec / 1000 % 60)//
		// 0
		if (now > end) {
			hr = 0
			min = 0
			sec = 0
			return 0
		}else{
			//
			hr = hr > 9 ? hr : '0' + hr
			min = min > 9 ? min : '0' + min
			sec = sec > 9 ? sec : '0' + sec
		}
		
		return {
			hr,
			min,
			sec
		}
					
}